.alg-wc-civs-original-select{
	display:none;
}

table.variations{
	tr:last-child{
		.alg-wc-civs-attribute{
			margin-bottom:22px;
		}
	}
}

.alg-wc-civs-attribute{
	font-size:0;
}

.alg-wc-civs-term{
	display:inline-block;
	min-width:36px;
	height:36px;
	margin:0 15px 0 0;
	cursor:pointer;
	opacity:0.5;
	transition:all 0.3s ease-in-out;
	border:2px solid #ccc;
	line-height:31px;
	font-weight:bold;
	background-color:fff;
	background-size:cover;
	text-align:center;
	&:not(.disabled){
		&:hover, &.active{
			opacity:1;
		}
		&.active{
			border-color:#333;
			box-shadow: 5px 5px 10px 0px rgba(0,0,0,0.35);
		}	
	}

	&.label{
		color:#333;
		font-size:17px;
		text-align:center;
		padding:0 8px;		
	}

	&.color{
		
	}
	
}

	
